import React, { Component } from "react";
import { connect } from "react-redux";
import { finishSend, progressSend } from "../actions/LogicAction";
import { gagal, warning } from "../Alert";
import { AxiosMasterGet } from "../Axios";
import { getToday } from "../getDate";
import { checkLaporan } from "../helper";
import FormKeuangan from "./Component/FormKeuangan";
import LaporanPrintBank from "./Component/LaporanPrintBank";
import LaporanPrintBankCard from "./Component/LaporanPrintBankCard";
import LaporanPrintBankSummary from "./Component/LaporanPrintBankSummary";
import LaporanPrintCash from "./Component/LaporanPrintCash";
import LaporanPrintCashCard from "./Component/LaporanPrintCashCard";
import LaporanPrintCashSummary from "./Component/LaporanPrintCashSummary";

class laporanKeuangan extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  handleCash(data) {
    switch (data.type) {
      case "SIMPLE":
        this.props.dispatch(progressSend());
        AxiosMasterGet("report/keuangan-cash")
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanPrintCash(
                localStorage.getItem("username"),
                getToday(),
                localStorage.getItem("username"),
                res.data
              );
            }
          })
          .catch((err) =>
            gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
              this.props.dispatch(finishSend())
            )
          );
        break;
      case "CARD":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          `report/keuangan-cash-card/KAS&${data.tglFrom}&${data.tglTo}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanPrintCashCard(
                localStorage.getItem("username"),
                getToday(),
                localStorage.getItem("username"),
                res.data
              );
            } else {
              return false;
            }
          })
          .catch((err) =>
            gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
              this.props.dispatch(finishSend())
            )
          );
        break;
      case "SUMMARY":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          `report/keuangan-cash-summary/KAS&${data.tglFrom}&${data.tglTo}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanPrintCashSummary(
                localStorage.getItem("username"),
                getToday(),
                localStorage.getItem("username"),
                res.data
              );
            }
          })
          .catch((err) =>
            gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
              this.props.dispatch(finishSend())
            )
          );
        break;
      default:
        warning("Silahkan PIlih Type Laporan");
        break;
    }
  }
  handleTransfer(data) {
    switch (data.type) {
      case "SIMPLE":
        this.props.dispatch(progressSend());
        AxiosMasterGet("report/keuangan-bank")
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanPrintBank(
                localStorage.getItem("username"),
                getToday(),
                localStorage.getItem("username"),
                res.data
              );
            }
          })
          .catch((err) =>
            gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
              this.props.dispatch(finishSend())
            )
          );
        break;
      case "CARD":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          `report/keuangan-bank-card/${data.no_rekening || "CASH"}&${
            data.tglFrom
          }&${data.tglTo}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanPrintBankCard(
                localStorage.getItem("username"),
                getToday(),
                localStorage.getItem("username"),
                res.data
              );
            }
          })
          .catch((err) =>
            gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
              this.props.dispatch(finishSend())
            )
          );
        break;
      case "SUMMARY":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          `report/keuangan-bank-summary/${data.kode_bank || "CASH"}&${
            data.tglFrom
          }&${data.tglTo}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanPrintBankSummary(
                localStorage.getItem("username"),
                getToday(),
                localStorage.getItem("username"),
                res.data
              );
            }
          })
          .catch((err) =>
            gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
              this.props.dispatch(finishSend())
            )
          );
        break;
      default:
        warning("Silahkan PIlih Type Laporan");
        break;
    }
  }
  handleOutstand(data) {}
  handleLainLain(data) {}
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                    <li className="nav-item">
                      <a
                        href="#cash-tab"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link active"
                      >
                        <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">Cash</span>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#transfer-tab"
                        data-toggle="tab"
                        aria-expanded="true"
                        className="nav-link "
                      >
                        <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">Transfer</span>
                      </a>
                    </li>
                   
                  </ul>
                  <div className="tab-content">
                    <div className="tab-pane show active" id="cash-tab">
                      <FormKeuangan
                        from="CASH"
                        onSubmit={(data) => this.handleCash(data)}
                      />
                    </div>
                    <div className="tab-pane " id="transfer-tab">
                      <FormKeuangan
                        from="TRANSFER"
                        onSubmit={(data) => this.handleTransfer(data)}
                      />
                    </div>

                    <div className="tab-pane" id="history-tab">
                      <FormKeuangan
                        from="OUTSTAND TRANSFER"
                        onSubmit={(data) => this.handleOutstand(data)}
                      />
                    </div>
                    <div className="tab-pane" id="lain-lain-tab">
                      <FormKeuangan
                        from="LAIN-LAIN"
                        onSubmit={(data) => this.handleLainLain(data)}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect()(laporanKeuangan);
