import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getrekeningList } from "../../actions/rekeningAction";
import { getToday } from "../../GlobalComponent/function";

const renderField = ({
  input,
  label,
  type,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input {...input} type={type} className="form-control" />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FormKeuangan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      type_laporan: "SIMPLE",
      kategori: (
        <>
          <option value="SEMUA" key="SEMUA">
            SEMUA
          </option>
          <option value="AMG" key="AMG">
            AMG
          </option>
          <option value="ARISAN FUQING" key="ARISAN FUQING">
            ARISAN FUQING
          </option>
          <option value="ARISAN SBTH" key="ARISAN SBTH">
            ARISAN SBTH
          </option>
          <option value="ARISAN THAO" key="ARISAN THAO">
            ARISAN THAO
          </option>
          <option value="ARISAN TRI BHAKTI" key="ARISAN TRI BHAKTI">
            ARISAN TRIBHAKTI
          </option>
          <option value="ARTOS" key="ARTOS">
            ARTOS
          </option>
          <option value="BAYAR HUTANG BELI LM" key="BAYAR HUTANG BELI LM">
            BAYAT HUTANG BELI LM
          </option>
          <option value="BAYAR HUTANG SUPLLIER" key="BAYAR HUTANG SUPLLIER">
            BAYAR HUTANG SUPLLIER
          </option>
          <option value="BAYAR HUTANG CUSTOMER" key="BAYAR HUTANG CUSTOMER">
            BAYAR HUTANG CUSTOMER
          </option>
          <option value="BAYAR PIUTANG JUAL LM" key="BAYAR PIUTANG JUAL LM">
            BAYAR PIUTANG JUAL LM
          </option>
          <option value="BAYAR PIUTANG RUPIAH" key="BAYAR PIUTANG RUPIAH">
            BAYAR PIUTANG RUPIAH
          </option>
          <option value="BUNGA" key="BUNGA">
            BUNGA
          </option>
          <option value="BUNGA BANK" key="BUNGA BANK">
            BUNGA BANK
          </option>
          <option value="CASHBACK PABRIK" key="CASHBACK PABRIK">
            CASHBACK PABRIK
          </option>
          <option value="JUAL LM" key="JUAL LM">
            JUAL LM
          </option>
          <option value="KARYAWAN" key="KARYAWAN">
            KARYAWAN
          </option>
          <option value="KAS AWAL" key="KAS AWAL">
            KAS AWAL
          </option>
          <option value="LING" key="LING">
            LING
          </option>
          <option value="OK MASAK" key="OK MASAK">
            OK MASAK
          </option>
          <option value="OPERASIONAL" key="OPERASIONAL">
            OPERASIONAL
          </option>
          <option value="PAJAK BANK" key="PAJAK BANK">
            PAJAK BANK
          </option>
          <option value="PRIVE" key="PRIVE">
            PRIVE
          </option>
          <option value="PRODUKSI" key="PRODUKSI">
            PRODUKSI
          </option>
          <option value="RALAT" key="RALAT">
            RALAT
          </option>
          <option value="STOR BANK" key="STOR BANK">
            STOR BANK
          </option>
          <option value="TABUNG TITIP CUSTOMER" key="TABUNG TITIP CUSTOMER">
            TABUNG TITIP CUSTOMER
          </option>
          <option value="TITIP SUPPLIER" key="TITIP SUPPLIER">
            TITIP SUPPLIER
          </option>
        </>
      ),
    };
  }

  componentDidMount() {
    this.props.dispatch(getrekeningList());
  }
  render() {
    switch (this.props.from) {
      case "CASH":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-4">
                  <div className="form-group">
                    <label htmlFor="">Type Laporan</label>
                    <Field
                      className="form-control"
                      name="type"
                      component="select"
                      onChange={(e) =>
                        this.setState({
                          type_laporan: e.target.value,
                        })
                      }
                    >
                      <option value="DEFAULT">SILAHKAN PILIH</option>
                      <option value="SIMPLE">SIMPLE</option>
                      <option value="CARD">CARD</option>
                      <option value="SUMMARY">SUMMARY</option>
                    </Field>
                  </div>
                </div>
                {/* <div
                  className={
                    this.state.type_laporan === "SIMPLE"
                      ? "col-lg-4 invisible"
                      : "col-lg-4"
                  }
                >
                  <div className="form-group">
                    <label htmlFor="">Kode Kas</label>
                    <Field
                      className="form-control"
                      name="kode"
                      component="select"
                    >
                      <option value="CASH">CASH</option>
                      <option value="KAS">KAS</option>
                    </Field>
                  </div>
                </div> */}
                {/* <div className="col-lg-4"></div> */}

                <div
                  className={
                    this.state.type_laporan === "SIMPLE"
                      ? "col-lg-4 d-none"
                      : "col-lg-4"
                  }
                >
                  <Field
                    type="date"
                    label="Dari Tanggal"
                    name="tglFrom"
                    component={renderField}
                  />
                </div>

                <div
                  className={
                    this.state.type_laporan === "SIMPLE"
                      ? "col-lg-4 d-none"
                      : "col-lg-4"
                  }
                >
                  <Field
                    type="date"
                    label="Sampai Tanggal"
                    name="tglTo"
                    component={renderField}
                  />
                </div>
              </div>
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button
                  className="btn btn-primary"
                  type="submit"
                  disabled={this.props.onSend}
                >
                  {this.props.onSend ? (
                    <span
                      className="spinner-border spinner-border-sm mr-3"
                      aria-hidden="true"
                    ></span>
                  ) : (
                    <i className="fa fa-print mr-2"></i>
                  )}
                  Print
                </button>
              </div>
            </div>
          </form>
        );
      case "TRANSFER":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-4">
                  <div className="form-group">
                    <label htmlFor="">Type Laporan</label>
                    <Field
                      className="form-control"
                      name="type"
                      component="select"
                      onChange={(e) =>
                        this.setState({
                          type_laporan: e.target.value,
                        })
                      }
                    >
                      <option value="DEFAULT">SILAHKAN PILIH</option>
                      <option value="SIMPLE">SIMPLE</option>
                      <option value="CARD">CARD</option>
                      <option value="SUMMARY">SUMMARY</option>
                    </Field>
                  </div>
                </div>
                <div
                  className={
                    this.state.type_laporan === "SIMPLE"
                      ? "col-lg-4 invisible"
                      : this.state.type_laporan === "SUMMARY"
                      ? "col-lg-4 invisible"
                      : "col-lg-4"
                  }
                >
                  <div className="form-group">
                    <label htmlFor="">Nomor Rekening</label>
                    <Field
                      className="form-control"
                      name="no_rekening"
                      component="select"
                    >
                      <option value="ALL">PILIH NOMOR REKENING</option>
                      {this.props.listBank.map((list) => (
                        <option value={list.no_rekening}>
                          {list.no_rekening} - {list.atas_nama}
                        </option>
                      ))}
                    </Field>
                  </div>
                </div>
                <div
                  className={
                    this.state.type_laporan === "SIMPLE"
                      ? "col-lg-4 invisible"
                      : this.state.type_laporan === "CARD"
                      ? "col-lg-4 invisible"
                      : "col-lg-4"
                  }
                >
                  <div className="form-group">
                    <label htmlFor="">Nama Bank</label>
                    <Field
                      className="form-control"
                      name="kode_bank"
                      component="select"
                    >
                      <option value="ALL">PILIH BANK</option>
                      {this.props.listBank.map((list) => (
                        <option value={list.nama_bank}>{list.nama_bank}</option>
                      ))}
                    </Field>
                  </div>
                </div>
                <div
                  className={
                    this.state.type_laporan === "SIMPLE"
                      ? "col-lg-4 d-none"
                      : "col-lg-4"
                  }
                >
                  <Field
                    type="date"
                    label="Dari Tanggal"
                    name="tglFrom"
                    component={renderField}
                  />
                </div>

                <div
                  className={
                    this.state.type_laporan === "SIMPLE"
                      ? "col-lg-4 d-none"
                      : "col-lg-4"
                  }
                >
                  <Field
                    type="date"
                    label="Sampai Tanggal"
                    name="tglTo"
                    component={renderField}
                  />
                </div>
              </div>
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button
                  className="btn btn-primary"
                  type="submit"
                  disabled={this.props.onSend}
                >
                  {this.props.onSend ? (
                    <span
                      className="spinner-border spinner-border-sm mr-3"
                      aria-hidden="true"
                    ></span>
                  ) : (
                    <i className="fa fa-print mr-2"></i>
                  )}
                  Print
                </button>
              </div>
            </div>
          </form>
        );
      case "OUTSTAND TRANSFER":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-3">
                  <div className="form-group">
                    <label htmlFor="">Kategori</label>
                    <Field
                      className="form-control"
                      name="kategori"
                      component="select"
                    >
                      <option value="SEMUA" key="SEMUA">
                        SEMUA
                      </option>
                      <option value="AMG" key="AMG">
                        AMG
                      </option>
                    </Field>
                  </div>
                </div>
                <div className="col-lg-3">
                  <Field
                    type="text"
                    label="."
                    name="keterangan"
                    component={renderField}
                    normalize={(value) => value && value.toUpperCase()}
                  />
                </div>
                <div className="col-lg-3">
                  <Field
                    type="number"
                    label="Jenis"
                    name="jenis"
                    component={renderField}
                  />
                </div>
                <div className="col-lg-3"></div>
                <div className="col-lg-4">
                  <Field
                    type="date"
                    label="Dari Tanggal"
                    name="tglFrom"
                    component={renderField}
                  />
                </div>

                <div className="col-lg-4">
                  <Field
                    type="date"
                    label="Sampai Tanggal"
                    name="tglTo"
                    component={renderField}
                  />
                </div>
              </div>
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button
                  className="btn btn-primary"
                  type="submit"
                  disabled={this.props.onSend}
                >
                  {this.props.onSend ? (
                    <span
                      className="spinner-border spinner-border-sm mr-3"
                      aria-hidden="true"
                    ></span>
                  ) : (
                    <i className="fa fa-print mr-2"></i>
                  )}
                  Print
                </button>
              </div>
            </div>
          </form>
        );
      case "LAIN-LAIN":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-4">
                  <Field
                    type="date"
                    label="Dari Tanggal"
                    name="tglFrom"
                    component={renderField}
                  />
                </div>

                <div className="col-lg-4">
                  <Field
                    type="date"
                    label="Sampai Tanggal"
                    name="tglTo"
                    component={renderField}
                  />
                </div>
              </div>
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary" type="submit">
                  <i className="fa fa-print mr-3"></i>Print
                </button>
              </div>
            </div>
          </form>
        );
      default:
        break;
    }
  }
}

FormKeuangan = reduxForm({
  form: "FormKeuangan",
  enableReinitialize: true,
})(FormKeuangan);
export default connect((state) => {
  return {
    listBank: state.provinsi.rekening,
    onSend: state.provinsi.onSend,
    initialValues: { tglFrom: getToday(), tglTo: getToday() },
  };
})(FormKeuangan);
