import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatRp, getUserID, logo_ayu_base64 } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPrintCashSummary = (
  username = "",
  tanggal = "",
  validby = "",
  data
) => {
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];

  let tableColumn = [
    [
      {
        content: `KODE KAS`,
      },
      {
        content: `SALDO AWAL`,
        styles: { halign: "right" },
      },
      {
        content: `SALDO IN`,
        styles: { halign: "right" },
      },
      {
        content: `SALDO OUT`,
        styles: { halign: "right" },
      },
      {
        content: `SALDO AKHIR`,
        styles: { halign: "right" },
      },
    ],
  ];
  let finalY = 40;
  doc.setFontSize(15);
  doc.text("LAPORAN KEUANGAN CASH - SUMMARY", 14, 30);
  doc.addImage(logo_ayu_base64, "PNG", 150, 10, 35, 12);

  doc.setFontSize(10);
  data.forEach((item, index) => {
    let rows = [
      item.kode_kas,
      { content: formatRp(item.saldo_awal), styles: { halign: "right" } },
      { content: formatRp(item.saldo_in), styles: { halign: "right" } },
      { content: formatRp(item.saldo_out), styles: { halign: "right" } },
      { content: formatRp(item.saldo_akhir), styles: { halign: "right" } },
    ];
    tableRows.push(rows);
  });
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    startY: 35,
    theme: "plain",
    rowPageBreak: "avoid",

    margin: { top: 20 },
    bodyStyles: { lineColor: "#000",fontSize : 8, },
    headStyles: {
      lineColor: "#000",fontSize : 8,
      fillColor: [212, 212, 211],
    },
    columnStyles: {
      2: { cellWidth: 40 },
      // etc
    },
  });
  finalY = doc.autoTableEndPosY() + 10;
  tableRows = [];

  doc.text(`Cetak By	: ${getUserID()}`, 14, finalY + 10);

  doc.text(`Cetak	Date : ${tanggal}`, 14, finalY + 16);

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN KEUANGAN CASH - SUMMARY",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN KEUANGAN CASH - SUMMARY</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPrintCashSummary;
